.about {
  padding: 40px;
}

.about-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.picture {
  height: 420px;
}

.about-info {
  font-size: 24px;
}

.info-name {
  color: #ff4949;
  font-size: 32px;
}

@media only screen and (max-width: 720px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.picture {
  height: 650px;
}

.about-info {
  font-size: 20px;
}

.info-name {
  font-size: 26px;
}
